import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'


import Vue from 'vue'
Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
    ...App
})
app.$mount()
// 时间格式过滤器
Vue.filter('formatCount',function(value){
	if(value >= 10000 && value <= 100000000){
		value /= 1000;
		return value.toFixed(1)+'万';
	}
	else if(value>100000000){
		value /= 100000000;
		return value.toFixed(1)+'亿'
	}
	else{
		return value;
	}
})